header {
  width: 100%;
  height: 130px;
  border-bottom: 1px solid #ccc;
  background: #fff;
}
header .inner {
  width: 100%;
  max-width: 1080px;
  height: 100%;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
header .inner nav {
  height: 100%;
}
header .inner nav * {
  height: 100%;
}
header .inner nav ul {
  display: flex;
}
header .inner nav ul li {
  width: 100px;
  border-left: 1px solid #ccc;
}
header .inner nav ul li:last-child {
  border-right: 1px solid #ccc;
}
header .inner nav ul li:hover a, header .inner nav ul li.act a {
  background: #75a8ea;
  color: #fff;
}
header .inner nav ul li:hover a span, header .inner nav ul li.act a span {
  color: #fff;
}
header .inner nav ul li a {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  font-size: 14px;
  transition: all 0.3s;
}
header .inner nav ul li a span {
  height: auto;
  font-size: 10px;
  color: #75a8ea;
  transition: all 0.3s;
}

.contact_bnr {
  width: 840px;
  margin: 0 auto;
}
.contact_bnr .bnr_ttl {
  width: 100%;
  height: 45px;
  background: #478ab2;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
  font-weight: bold;
  font-size: 16px;
}
.contact_bnr .bnr_col {
  width: 100%;
  height: 90px;
  display: flex;
  align-items: center;
  padding: 0 20px;
  border: 1px solid #dbdbdb;
  border-top: none;
}
.contact_bnr .bnr_col p.txt {
  font-size: 16px;
  color: #478ab2;
}
.contact_bnr .bnr_col p.tel {
  margin-left: auto;
  font-size: 46px;
}
.contact_bnr .bnr_col p.fax {
  margin-left: 15px;
  font-size: 20px;
}

footer {
  margin-top: 120px;
  width: 100%;
  background: #333;
  padding: 25px 0 30px;
}
footer ul {
  display: flex;
  justify-content: center;
  margin-bottom: 30px;
}
footer ul li {
  padding: 1px 0;
}
footer ul li:not(:last-child) {
  padding-right: 15px;
  margin-right: 15px;
  border-right: 1px dotted #fff;
}
footer ul li a {
  font-size: 14px;
  color: #fff;
  line-height: 1;
}
footer p {
  text-align: center;
  line-height: 1;
}
footer p small {
  font-size: 12px;
  color: #7f7f7f;
}